___CSS_LOADER_EXPORT___.push([module.id, `
.skip-link {
  position: absolute;
  top: -50px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
a{
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}
.navbar {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  background-color: #ff8800;
  padding: 10px;
  font-size: 18px;
}
.navbar div a {
  font-family: 'Dancing Script', cursive;
  text-decoration: none;
  color: black;
  font-size: 30px;
}
.nav-list {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
}

.nav-item a{
  text-decoration: none;
  color: black;
  padding: 10px;
  font-size: 20px;
}
.nav-item a:hover {
  color: white;
}


.hamburger {
  display: none; 
  cursor: pointer;
  background-color: #ff8800;
  border: none;
}

.hero {
  position: relative;
  text-align: center; 
  overflow: hidden;
}

.hero img {
  width: 100%;
  height: auto;
}

.hero-text {
  position: absolute;
  top: 50%; 
  left: 50%; 
  transform: translate(-50%, -50%); 
  color: white; 
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); 
  background-color: rgba(218, 215, 215, 0.5); 
  border-radius: 5px;
  padding: 20px;
}

.hero-text h1 {
  font-family: 'Dancing Script', cursive;
  font-size: 3rem; 
}

.hero-text p {
  font-size: 18px; 
}


main {
  width: 100%;
  margin-inline: auto;
  padding: 2rem 4rem;
}

.tentang-kami {
  flex: 1;
  display: flex;
  align-items: center;
}
.tentang-kami .img-profile {
  display: flex;
  flex: 1;
} 

.tentang-kami .description {
  flex: 1 1;
}

.tentang-kami .description .title {
  font-size: 3.5rem;
}

.tentang-kami .img-profile img {
  margin: auto;
  min-width: 250px;
  width: 300px;
}

.tentang-kami .description p {
  font-size: 1.2rem;
  margin-top: 10px;
}

.katalog-container .katalog-title {
  display: flex;
  justify-content: center;
  font-size: 2rem;
  margin-top: 20px;
}


#list-katalog, #favoriteResto {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
  gap: 20px;
  margin-top: 10px; 
}

.card-resto {
  border: 3px solid #ff8800;
  border-radius: 5px;
  padding: 20px;
  text-align: center;
  background-color: white;
  
}

.card-a {
  text-decoration: none;
  color: black;
}

.image {
  max-width: 100%;
  height: auto;
}

.rating {
  font-size: 18px;
  color: black;
  margin-top: 10px;
}

.card-content-title {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
  text-decoration: none;
}

.content-description {
  font-size: 16px;
  text-align: justify;
  overflow: hidden;
  text-decoration: none;
}

.footer {
  background-color: #ff8800;

}

.footer-title {
  padding: 10px;
  text-align: center;
}

@media screen and (max-width: 1200px) {
  #list-katalog, #favoriteResto {
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    gap: 20px;
    margin-top: 10px; 
  }
}

@media screen and (max-width: 768px) {

  .hero-text h1 {
    font-size: 2rem;
  }
  .tentang-kami {
    flex-direction: column;
    align-items: center;
  }

  .tentang-kami .description .title {
    display: flex;
    font-size: 2.5rem;
    justify-content: center;
  }

  .content-description {
    max-width: 100%;
  }

  #list-katalog, #favoriteResto {
    display: grid;
    grid-template-columns: repeat(1, 1fr); 
    gap: 20px;
    margin-top: 10px; 
  }

  .hamburger {
    display: block; 
  }

  .nav-list {
    display: none; 
  }
  
  .navbar.open .nav-list {
    display: flex;
    flex-direction: column;
    background-color: #ff8800; 
    color: white;
    position: absolute;
    top: 55px; 
    left: 0;
    right: 0;
    z-index: 1000;
  }

  .navbar.open .nav-list .nav-item {
    text-align: center;
    padding: 5px;
  }
}

@media screen and (max-width: 410px) {
  .tentang-kami .description .title {
    display: flex;
    font-size: 2rem;
    justify-content: center;
  }
}

@media screen and (max-width: 360px) {
  .hero-text h1 {
    font-size: 1rem;
  }
}



